.cartmain{
  padding: 20px 0;
  margin: 0 70px;
  border-bottom: 2px solid #f7f9fc;
}
.cartmain:last-child{
  border-bottom: 2px solid #2b2e33;
}
.cartmain ul{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  list-style: none;
  -ms-flex-align: center;
      align-items: center;
  padding: 0;
}
.cartmain ul li{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.cartmain ul li img{
  width: 96px;
  height: 96px;

}
.cartmain ul li input{
  width: 20px;
  border:0;
}
.cartmain ul li:last-child{
  display: none;
}
.cartmain ul li p{
  margin-left: 20px
}
.cartmain ul li a{
  text-decoration: none;
  color:black;
}
.cartmain .cart_add, .cartmain .cart_sub{
  display: block;
  width: 36px;
  height: 36px;
  border: 1px solid #4f545c;
  text-align: center;
  line-height: 36px;
}



